/* ChatbotApp.css */
.container {
    width: 100%;
    max-width: 100vw;
    height: 80vh;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    background-color: #000000;
  }
.chatbot-container {
    width: 100%;
    max-width: 800px;
    height: 100vh;
    margin: auto auto;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    background-color: #000000;
  }
  
  .chatbot-header {
    padding: 15px;
    background-color: #075e54;
    color: white;
    border-radius: 10px 10px 0 0;
    text-align: center;
  }
  
  .messages-container {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .message {
    max-width: 70%;
    padding: 10px 15px;
    border-radius: 15px;
    word-wrap: break-word;
  }
  
  .user-message {
    align-self: flex-end;
    background-color: #dcf8c6;
  }
  
  .ai-message {
    align-self: flex-start;
    background-color: white;
  }
  
  .message-content {
    line-height: 1.4;
  }
  
  .input-form {
    display: flex;
    padding: 15px;
    background-color: white;
    border-top: 1px solid #e0e0e0;
    border-radius: 0 0 10px 10px;
  }
  
  input {
    flex: 1;
    padding: 10px;
    border: 1px solid #e0e0e0;
    border-radius: 20px;
    outline: none;
    font-size: 16px;
  }
  
  button {
    margin-left: 10px;
    padding: 10px 15px;
    background-color: #075e54;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-weight: bold;
  }
  
  button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .empty-state {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #757575;
    text-align: center;
  }
  
  .loading {
    display: flex;
    justify-content: center;
  }
  
  .loading .dot {
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background-color: #999;
    margin: 0 3px;
    display: inline-block;
    animation: pulse 1.5s infinite ease-in-out;
  }
  
  .loading .dot:nth-child(2) {
    animation-delay: 0.3s;
  }
  
  .loading .dot:nth-child(3) {
    animation-delay: 0.6s;
  }
  
  @keyframes pulse {
    0%, 100% {
      transform: scale(0.8);
      opacity: 0.6;
    }
    50% {
      transform: scale(1.2);
      opacity: 1;
    }
  }
  